.wrapper {
    height: 100%;
}

.addPost {
    width: 100%;
    box-sizing: border-box;
    margin-top: 15px;
    border-radius: 5px;
}

.addPost form {
    width: 100%;
    display: flex;
    justify-content: space-between;
    box-sizing: border-box;
    margin: 0;
    padding: 10px;
    background-color: rgba(255, 255, 255, 0.151);
    border-radius: 5px;
}


.addPost form textarea {
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
    padding: 5px 10px;
    font-size: 0.8em;
    color: #fff;
    background-color: rgba(255, 255, 255, 0.151);
    resize: none;
    border-radius: 5px;
    border: none;
    width: 85%;
    height: 50px;
    margin-right: 10px;
    scrollbar-width: none;
}

.addPost form textarea::-webkit-scrollbar {
    width: 0px;
    margin-right: 10px;
}

.addPost form textarea::placeholder {
    color: rgba(255, 255, 255, 0.755);
}

.Button {
    margin: auto 0;
    height: 35px; 
    width: calc(15% - 30px);
    min-width: fit-content;
}

.Button button {
    margin: 0;
    border: none;
    outline: none;
    color: #fff;
    cursor: pointer;
    background-color: rgba(255, 255, 255, 0.151);
    border-radius: 5px;
    height: 100%;
    width: 100%;
}

.Button :hover {
    background-color: rgba(255, 255, 255, 0.367);
}